
import { mapGetters } from 'vuex'
import { PowerSelect } from 'components/index.js'
import { DatePicker } from 'cube-ui'
import { formatDate, toThousand, formatNumber } from 'common/js/Util.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import CheckIcon from 'base/check-icon/check-icon.vue'

export default {
props: {
readonly: {
type: Boolean,
default: false
},
required: {
type: Boolean,
default: false
},
disabled: {
type: Boolean,
default: false
},
value: null,
KeyWord: {
type: String,
default: ''
},
field: {
type: String,
default: ''
},
mainformData: {
type: Object,
default () {
return {}
}
},
label: {
type: String,
default: ''
},
type: {
type: String,
default: ''
},
ToFixed: {
type: Number,
default: 2
},
placeholder: {
type: String,
default: '请输入'
},
comboboxdata: {
type: Object,
default () {
return {}
}
}
},
data () {
return {
rowValue: '',
rowType: 'text',
canEdit: false,
dataPicker: {
title: '日期',
min: new Date(1920, 1, 1),
max: new Date(2100, 12, 30),
value: new Date(),
dateShowFormat: {year: 'YYYY', month: 'MM', date: 'DD'}
}
}
},
computed: {
...mapGetters([
'formStatus'
])
},
mounted () {
this.setData()
},
methods: {
setData () {
this.$nextTick(() => {
let value = this.mainformData[this.field]
this.rowType = this.type
if (this.readonly || this.disabled || this.formStatus === 'view') {
this.canEdit = true
}

if (this.rowType.indexOf('yyyy') > -1) {
this.rowType = 'datePicker'
}

switch (this.rowType) {
case 'datePicker':
this.rowValue = formatDate(value)
break
case 'number':
if (!value || value === '') {
value = 0
}
this.rowValue = parseFloat(value).toFixed(2)
break
case 'n2':
if (!value || value === '') {
value = 0
}

this.rowValue = value = toThousand(value)
break
case 'percent':
if (!value || value === '') {
value = 0
}
this.rowValue = parseFloat(Number(value) * 100).toFixed(2)
break
case 'rate':
if (!value || value === '') {
value = 0
}

value += ''

let main = value.split('.')[0]
let tail = value.split('.')[1]

this.rowValue = `${toThousand(main, true)}.${formatNumber(tail, 8)}`
break
default:
this.rowValue = value
}
})
},
change (value) {
this.rowValue = value
},
changeCheckBox () {
if (this.readonly === true || this.disabled === true) {
return false
}

let checked = this.rowValue === '1'

if (checked) {
this.rowValue = '0'
} else {
this.rowValue = '1'
}
},

showDatePicker () {
if (this.disabled || this.readonly) {
return false
} else {
this.$refs.updateDataPicker.show()
}
},

selectDatePicker (selectDate) {
this.rowValue = formatDate(selectDate)
}
},
watch: {
mainformData (mainformData) {
this.setData()
},
rowValue (newRowValue) {
if (this.timer) {
clearTimeout(this.timer)
}

this.timer = setTimeout(() => {
let item = {
KeyWord: this.KeyWord,
field: this.field,
value: newRowValue
}

if (this.type === 'money') {
item.value = newRowValue.replace(',', '')
}

if (this.type === 'percent') {
item.value = parseFloat(newRowValue) / 100
}

if (this.KeyWord === '') {
throw Error('your KeyWord is null, set it please')
}
if (this.field === '') {
throw Error('your field is null, set it please')
}

this.$emit('enterChange', item)
}, 500)
}
},
components: {
PowerSelect,
XTextarea,
Group,
DatePicker,
CheckIcon
}
}



